*{
    padding: 0px;
    margin: 0px;
}
.wrapper{
    background: #ffffff;
    box-shadow: 0 0 15px 0 rgba(31, 56, 88, 0.08);
    border-radius: 3px;
    position: relative;
    overflow: hidden;
}
.list-wrap{
    position: absolute;
}
.first{
    width: 400px;
    color:#3c99d8;
}
.city{
    width: 200px;
}
.min{
    width: 70px;
}
.more-positon{
    height: 49px;
    border: rgba(31, 56, 88, 0.06) solid 1px;
    line-height: 49px;
    padding: 0 14px;
    background: #ffffff;
    font-size: 14px;
    color: #000000;
    position: relative;
    z-index: 2;
}
.more-positon a{
    float: right;
    font-size: 14px;
    color: rgba(31, 56, 88, 0.6);
    cursor: pointer;
    text-decoration: none;
}
.list-item{
    width: 100%;
    display: flex;
    list-style-type: none;
    overflow: hidden;
    line-height: 50px;
    padding: 0 14px;
    justify-content: space-between;
}
.list-item{
    text-align: left;
}